<template>
  <div class="exchangeWrap">
      <header>
          <router-link to="/benefit">
          <i class="el-icon-arrow-left"></i>
          </router-link>
          <div>兑换红包</div>
      </header>
      <div class="exchange_code">
          <input type="text" placeholder="请输入兑换码" class="exchange_input" v-model="input1">
          <section class="input_container captcha_code_container">
              <input type="text" placeholder="验证码" maxlength="4" v-model="input2">
              <div class="img_change_img">
                  <img :src="capUrl" alt="">
                  <div class="change_img">
                      <p>看不清</p>
                      <p @click="changeCap">换一张</p>
                  </div>
              </div>
          </section>
          <div class="determine" @click="duihuan">兑换</div>
          <section class="tip_text_container" v-if="isshow">
          <div class="tip_icon">
              <span></span>
              <span></span>
          </div>
          <p class="tip_text">{{thisArr.message}}</p>
          <div class="confrim" @click="sure">确定</div>
      </section>
      </div>
  </div>
</template>

<script>
export default {
name:'Exchange',
data(){
    return{
        capUrl:"",
        input1:'',
        input2:'',
        userid:'',
        isshow:false,
        thisArr:''
    }
},
created(){
    this.requestCode();
    this.userid = localStorage.getItem('userid')
},
methods:{
    requestCode(){
        this.axios.post("/v1/captchas").then(res=>{
        this.capUrl = res.code;
    }).catch(err => {})
    },
    changeCap(){
        this.requestCode();
    },
    duihuan(){
        this.isshow = true
        this.axios.post("/v1/users/"+this.userid+"/hongbao/exchange",{
           user_id:this.userid,
           exchange_code:this.input1,
           captcha_code:this.input2
        }).then(res=>{
            // console.log(res)
            this.thisArr = res;
        }).catch(err => {})
    },
    sure(){
        this.isshow = false;
    }
}
}
</script>

<style scoped>
input{
    outline: none;
    border: 0;
    padding: 0;
}
.exchangeWrap{
    padding-top: 1.95rem;
    background-color: #f1f1f1;
}
header>a{
  position: absolute;
  left: 0.4rem;
  color: #fff;
  font-size: 1rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
header>div{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 50%;
    color: #fff;
    text-align: center;
    font-size: .8rem;
    font-weight: 700;
}
.exchange_code{
    margin-top: .7rem;
    padding: 0 .5rem;
}
.exchange_input{
    width: 100%;
    font-size: .7rem;
    color: #666;
    padding: .6rem .4rem;
    border-radius: .15rem;
}
.captcha_code_container{
    height: 2.2rem;
    display: flex;
    margin-top: .7rem;
}
.captcha_code_container input{
    font-size: .7rem;
    color: #666;
    padding: .4rem;
    border-radius: .15rem;
    flex: 3;
}
.captcha_code_container .img_change_img{
    display: flex;
    align-items: center;
    flex: 2;
    margin-left: .3rem;
    background-color: #fff;
    padding-left: .2rem;
    border-radius: .15rem;
}
.img_change_img img{
    width: 3.5rem;
    height: 1.5rem;
    margin-right: .2rem;
}
.img_change_img .change_img{
    display: flex;
    flex-direction: "column";
    flex-wrap: wrap;
    width: 2rem;
    justify-content: center;
}
.img_change_img .change_img p{
    font-size: .55rem;
    color: #666;
}
.img_change_img .change_img p:nth-of-type(2) {
    color: #3b95e9;
    margin-top: .2rem;
}
.active{
    background-color: #4cd964;
}
.determine{
    background-color: #4cd964;
    font-size: .7rem;
    color: #fff;
    text-align: center;
    margin: 0 .7rem;
    line-height: 1.8rem;
    border-radius: .2rem;
    margin-top: .7rem;
}
.tip_text_container{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -6rem;
    margin-left: -6rem;
    width: 12rem;
    animation: tipMove .4s;
    background-color: #fff;
    padding-top: .6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 1px;
    border-radius: .25rem;
}
.tip_text_container .tip_icon{
    width: 3rem;
    height: 3rem;
    border: .15rem solid #f8cb86;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.tip_icon span:first-of-type {
    width: .12rem;
    height: 1.5rem;
    background-color: #f8cb86;
}
.tip_icon span:nth-of-type(2) {
    width: .2rem;
    height: .2rem;
    border: 1px;
    border-radius: 50%;
    margin-top: .2rem;
    background-color: #f8cb86;
}
.tip_text{
    font-size: .7rem;
    color: #333;
    line-height: .9rem;
    text-align: center;
    margin-top: .8rem;
    padding: 0 .4rem;
}
.confrim{
    font-size: .8rem;
    color: #fff;
    font-weight: 700;
    margin-top: .8rem;
    background-color: #4cd964;
    width: 100%;
    text-align: center;
    line-height: 1.8rem;
    border: 1px;
    border-bottom-left-radius: .25rem;
    border-bottom-right-radius: .25rem;
}
</style>